/**
 *
 *调用本地摄像头
 * */
const CONSTRAINTS = {video: {width: 480, height: 320}}; //各种设置分辨率等
const PIXEL = 1;
const constraints = {
  audio: false,
  facingMode: 'user', // 优先调用前置摄像头,
  video: {
    width: {min: 1024, ideal: 1280, max: 1920},
    height: {min: 776, ideal: 720, max: 1080}
  }
};

/**
 * 1 访问用户媒体设备的兼容方法
 *
 * */
export function getUserMedia(videoWidth = 422, videoHeight = 310) {
  const constraints = {video: {width: videoWidth * PIXEL, height: videoHeight * PIXEL},facingMode: 'user',};
  return new Promise((resolve, reject) => {
    if (navigator.mediaDevices.getUserMedia) {//最新的标准API
      navigator.mediaDevices.getUserMedia(constraints).then(resolve).catch(reject);
    } else if (navigator.webkitGetUserMedia) {//webkit核心浏览器
      navigator.webkitGetUserMedia(constraints, resolve, reject);
    } else if (navigator.mozGetUserMedia) { //firfox浏览器
      navigator.mozGetUserMedia(constraints, resolve, reject);
    } else if (navigator.getUserMedia) {//旧版API
      navigator.getUserMedia(constraints, resolve, reject);
    } else {
      reject('不支持访问用户媒体')
    }
  })
}

export function setVideoPlay(stream, videoId) {
  const videoElement = document.getElementById(videoId);
  if (!videoElement) return false;
  if (videoElement.mozSrcObject !== undefined) {
    //Firefox中，video.mozSrcObject最初为null，而不是未定义的，我们可以靠这个来检测Firefox的支持
    videoElement.mozSrcObject = stream;
  } else {
    videoElement.src = window.URL && window.URL.createObjectURL(stream) || stream;
  }
  // videoElement.play();
  return videoElement;
}

export function capture(video, width, height, pixel = PIXEL) {
  return new Promise((resolve) => {
    let canvas = document.createElement('canvas');
    canvas.width = pixel * width;
    canvas.height = pixel * height;
    canvas.getContext('2d').drawImage(video, 0, 0, canvas.width, canvas.height);
    const base64data = canvas.toDataURL("image/png");
    resolve({base64data, width, height, pixel, date: +new Date()});
    canvas = null;
  })
}

// 0.0 将mime-type改为image/octet-stream,强制让浏览器下载
const fixType = (type) => {
  type = type.toLocaleLowerCase().replace(/jpg/i, 'jpeg');
  let r = type.match(/png|jpeg|bmp|gif/)[0];
  return 'image/' + r;
};
const saveFile = (data, filename) => {
  let save_link = document.createElementNS('http://www.w3.org/1999/xhtml', 'a');
  save_link.href = data;
  save_link.download = filename;
  let event = document.createEvent('MouseEvents');
  event.initMouseEvent('click', true, false, window, 0, 0, 0, 0, 0, false, false, false, false, 0, null);
  save_link.dispatchEvent(event);
}

/**
 * type: 1.确定图片的类型  获取到的图片格式 data:image/Png;base64,......
 * type 默认 'png';//你想要什么图片格式 就选什么吧
 *
 * */

// 下载文件到本地
export function downloadImage(base64data, filename, type = 'png') {
  const data  = base64data.replace(fixType(type), 'image/octet-stream');
  saveFile(data, `${filename}.${type}`);
}

/**
 *
 * 图片转成Buffer
 * */
function dataURItoBlob(dataURI) {
  const byteString = atob(dataURI.split(',')[1]);
  const mimeString = dataURI.split(',')[0].split(':')[1].split(';')[0];
  const ab = new ArrayBuffer(byteString.length);
  const ia = new Uint8Array(ab);
  for (let i = 0, len = byteString.length; i < len; i++) {
    ia[i] = byteString.charCodeAt(i);
  }
  return new Blob([ab], {type: mimeString});
}

// 把canvas转化为file
export function decodeCanvasToFile(canvas) {
  //这里转成的是base64的地址，直接用到img标签的src是可以显示图片的
  let src = canvas.toDataURL("image/png");
  let file = new FormData();
  const blob = dataURItoBlob(src);
  file.append('file', blob);
  return {file, src}
}

export function dataURLtoFile(dataUrl, filename) {//将base64转换为文件
  let arr = dataUrl.split(','), mime = arr[0].match(/:(.*?);/)[1],
    bstr = atob(arr[1]), n = bstr.length, u8arr = new Uint8Array(n);
  while (n--) {
    u8arr[n] = bstr.charCodeAt(n);
  }
  return new File([u8arr], filename, {type: mime});
}

const types = ['png', 'jpeg', 'jpg']

export function parseCanvasToFile(canvas, type) {
  type = types.includes(type) ? type : 'png';
  const dataUrl = canvas.toDataURL(`image/${type}`);
  const file = dataURLtoFile(dataUrl, `${+new Date()}.${type}`);
  return {file, src: dataUrl}
}

