import BMap from 'BMap'

export const initMap = (mapId, point) => {
  // 百度地图API功能
  const map = new BMap.Map(mapId);    // 创建Map实例
  map.centerAndZoom(new BMap.Point(113.39134,23.104104), 17);  // 初始化地图,设置中心点坐标和地图级别
  //添加地图类型控件
  map.addControl(new BMap.MapTypeControl({
    mapTypes: [BMAP_NORMAL_MAP, BMAP_HYBRID_MAP]
  }));
  // map.setCurrentCity("北京");          // 设置地图显示的城市 此项是必须设置的
  map.enableScrollWheelZoom(true);     //开启鼠标滚轮缩放

  return map;
}

export const newPoint = ([lng, lat]) => new BMap.Point(lng, lat);
// 移动定位
export const mapPanto = (map, point) => {
  // const point = new BMap.Point(lng, lat);
  map.addOverlay(new BMap.Marker(point));
  map.panTo(point);
}
// 根据城市名定位
export const locationByCityName = (callback) => {
  const myCity = new BMap.LocalCity();
  myCity.get(result => {
    callback && callback(result)
  });
}

// 根据ip定位
export const locationByIp = (callback) => {
  // 百度地图API功能
  const geolocation = new BMap.Geolocation();
  geolocation.getCurrentPosition(function (data) {
    if (this.getStatus() == BMAP_STATUS_SUCCESS) {
      // callback && callback(data)
      const {point, address: {city, province, street}} = data;
      callback && callback({point, address: `${province}${city}${street}`})
    } else {
      console.error(this.getStatus)
    }
  }, {enableHighAccuracy: true})
  //关于状态码
  //BMAP_STATUS_SUCCESS	检索成功。对应数值“0”。
  //BMAP_STATUS_CITY_LIST	城市列表。对应数值“1”。
  //BMAP_STATUS_UNKNOWN_LOCATION	位置结果未知。对应数值“2”。
  //BMAP_STATUS_UNKNOWN_ROUTE	导航结果未知。对应数值“3”。
  //BMAP_STATUS_INVALID_KEY	非法密钥。对应数值“4”。
  //BMAP_STATUS_INVALID_REQUEST	非法请求。对应数值“5”。
  //BMAP_STATUS_PERMISSION_DENIED	没有权限。对应数值“6”。(自 1.1 新增)
  //BMAP_STATUS_SERVICE_UNAVAILABLE	服务不可用。对应数值“7”。(自 1.1 新增)
  //BMAP_STATUS_TIMEOUT	超时。对应数值“8”。(自 1.1 新增)

  /**
   *
   * {
 "accuracy": null,
 "altitude": null,
 "altitudeAccuracy": null,
 "heading": null,
 "latitude": "23.12004910",
 "longitude": "113.30764968",
 "speed": null,
 "timestamp": null,
 "point": {
  "lng": 113.30764968,
  "lat": 23.1200491
 },
 "address": {
  "city": "广州市",
  "city_code": 0,
  "district": "",
  "province": "广东省",
  "street": "",
  "street_number": ""
 }
}
   *
   * */
}

export const getDistance = ({start: {point: start, address: startAddress}, end: {point: end, address: endAddress}}, map, callback) => {
  const distance = (map.getDistance(start, end)).toFixed(2)  //获取两点距离,保留小数点后两位
  callback &&  callback(`${startAddress}到${endAddress}的距离是: ${distance}米`)
  const polyline = new BMap.Polyline([start, end], {strokeColor:"red", strokeWeight:1, strokeOpacity:0.5});  //定义折线
  map.addOverlay(polyline);     //添加折线到地图上
}


export const parsePoint = (point, callback) => {
  // 创建地址解析器实例
  const geoc = new BMap.Geocoder();
  geoc.getLocation(point,  ({addressComponents: {province, city, district, street, streetNumber}}) => {
    callback && callback(`${province}${city}${district}${street}${streetNumber}`)
  });
}



