<template>
  <div class="capture" id="capture">
    <video id="video" :width="video.width" :height="video.height" autoplay v-show="!showPicture"></video>
    <div class="picture-list"  v-show="showPicture">
      <!--<img  class="show" v-for="(p, i) in pictures" :src="p.base64data">-->
      <el-scrollbar :style="{height: `${video.height}px`}">
        <span class="show" v-for="(p, i) in pictures" :key="`${i}span`">
          <img  class="show-img"  :src="p.base64data">
          <span class="modal">
            <el-button class="modal-btn" size="mini" @click="bingShowBigImg(p.base64data)">查看大图</el-button>
            <el-button class="modal-btn" size="mini" @click="bindDownLoad(p)">下载图片</el-button>
          </span>
        </span>
      </el-scrollbar>
    </div>
    <div class="btn-list">
      <el-button type="button" class="btn" @click="bindCapture">capture</el-button>
      <el-button type="button" class="btn" @click="showPicture = !showPicture">{{showPicture ? 'show video': 'see picture'}}</el-button>
    </div>
    <show-bimg ref="showBimg"><img :src="bimgSrc" slot="img"></show-bimg>
  </div>
</template>

<script>
  import { getUserMedia, setVideoPlay, capture, downloadImage} from './components/captureUtils'
  import ShowBimg from './components/showLargeImg'
  export default {
    components: { ShowBimg },
    data(){
      return {
        video: {width: 400, height: 400},
        stopVideo: null,
        videoIo: null,
        scale: 910.34 / 681.34,
        showPicture: false,
        pictures: [],
        bimgSrc: '' // 大图的src
      }
    },
    mounted(){
      this.initVideo()
    },
    methods: {
      initVideo(){
        this.$nextTick(() => {
          // 计算宽度
          const video = document.getElementById('video');
          const width = video.offsetWidth, height = video.offsetHeight;
          Object.assign(this.video, {width, height})

          // 2 开启摄像头
          getUserMedia(width*2, height*2).then(stream => {
            this.videoIo = setVideoPlay(stream, 'video');
            // this.videoIo.play();
            this.stopVideo = stream.getTracks()[0];
          }).catch(() => {
            this.stopVideo = null;
            this.videoIo = null;
            this.$message({message: '当前无法开启摄像头', type: 'error'});
            this.$emit('close', this.video)
          })
        })
      },
      bindCapture(){
        const {width, height} = this.video;
         /**
          * 控制尺寸, 就是控制width 和height
          * 注意保持长宽的比例
          *
          * */
        capture(this.videoIo, width, height).then(data => {
          this.$message.success('拍照成功!')
          this.pictures.push(data)
        })
      },
      bindDownLoad({base64data, date, width, height}){
        downloadImage(base64data, `img${date}w${width}h${height}`)
      },
      bingShowBigImg(src){
        this.bimgSrc = src;
        this.$refs.showBimg.showDialog = true;
      }
    }
  }
</script>

<style scoped rel="stylesheet/scss" lang="scss">
  .capture{
    width: 100%;
    height: 100%;
    .picture-list,#video{
      display: block;
      width: 100%;
      height: 80%;
      background: #000;
      padding: 0.2rem;
      border-radius: 0.2rem;
      border: 0.04rem solid #DCDFE6;
    }
    .picture-list{
      text-align: left;
      .show{
        display: inline-block;
        width: 2rem;
        margin: 0 0.1rem;
        min-height: 0.2rem;
        border: 0.05rem solid #409EFF;
        position: relative;
        .show-img{
          display: block;
          width: 100%;
        }
        .modal{
          display: none;
          position: absolute;
          left:0;
          top: 50%;
          width: 100%;
          height: 0.2rem;
          margin-top: -0.1rem;
          background: rgba(0,0,0,.2);
          text-align: center;
        }
        &:hover{
          .modal{
            display: block;
          }
        }
      }
    }
    .btn-list{
      width: 100%;
      margin-top: 0.2rem;
      padding: 0.2rem;
      line-height: 10%;
      background: #fff;
      text-align: center;
      .btn{
        width: 1rem !important;
      }
    }
  }
</style>



