<template>
  <div class="view-big-img">
    <el-dialog :visible.sync="showDialog" :width="widths" :before-close="handleClose">
      <div class="show-img">
        <slot name="img" class="img"/>
      </div>
    </el-dialog>
  </div>
</template>

<script>
  export default {
    props: {
      widths:{
        type:  String,
        default: '40%'
      }
    },
    data() {
      return {
        showDialog: false
      }
    },
    methods: {
      handleClose(){
        this.showDialog = false
      }
    }
  }
</script>

<style  rel="stylesheet/scss" lang="scss">
  .view-big-img{
    .el-dialog{
      background: transparent;
    }
    .el-dialog__header{
      padding: 0;
      display: none;
    }
    .show-img{
      width: 100%;
    }
    .el-dialog__body{
      padding: 0;
      background: transparent;
    }
    .img{
      display: block;
      width: 100%;
      border-radius: 5px;
    }
  }
</style>



