<template>
  <div class="baidu-map" id="map"></div>
</template>

<script>
  import { initMap, locationByCityName,  mapPanto , locationByIp, parsePoint,  getDistance, newPoint } from './components/bmap'
  export default {
    data(){
      return {
        points: {
          start: null, // {point, address}
          end: null
        },
        clickConuting: 0,
        map: null
      }
    },
    mounted(){
      const map = initMap('map')

      // locationByCityName(({name, center: {lng, lat}}) => {
      //   // console.log(`城市: ${name}, 经纬度: [${lng}, ${lat}]`)
      //   mapPanto(map, [lng, lat])
      // })

      // locationByIp(({point, address}) => {
      //   this.$message.success(`定位成功, 当前地址: ${address}`)
      //   mapPanto(map,point)
      // })

      this.map = map;
      map.addEventListener("click", this.bindClickMap);
    },
    methods: {
      bindClickMap({point}){
        const map = this.map;
        // 点击超过五次, 移除点击事件
        if(this.clickConuting > 5){
          this.removeClickEvent(map);
          map.clearOverlays();
          mapPanto(map,newPoint([113.39134,23.104104]))
          return false;
        }

        parsePoint(point, address => {
          mapPanto(map, point)
          const isStart = this.clickConuting % 2 === 0;
          Object.assign(this.points, {[isStart ? 'start': 'end']: {point, address}})

          if(isStart){
            getDistance(this.points, map, address => {
              this.$message.success(address);
              console.log(address);

            })
          }

        })

        this.clickConuting += 1;
      },
      removeClickEvent(map){
        map.removeEventListener("click", this.bindClickMap);
      }
    }
  }
</script>

<style scoped rel="stylesheet/scss" lang="scss">
.baidu-map{
  width: 100%;
  height: 100%;
  padding: 0.2rem;
  background: #fff;
  border: 0.1rem solid #fff;
}
</style>



